*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
#background_img{
  display: flex;
  height: 80vh;
  width: 100%;
  min-height: 600px;
  background: url("./images/clothes.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}
#round_title_bg{
  position: relative;
  display: flex;
  width: 50%;
  min-width: 320px;
  text-align: center;
  height: 45%;
  margin: auto;
  flex-direction: column;
}
#round_bg_color{
  position: absolute;
  height: 100%;
  width: 100%;
  background-color: #000;
  opacity: 0.3;
  border-radius: 50px;
}
#round_title_bg h1{
  font-size: 40px;
  margin: auto;
  color: #fff;
  z-index: 1;
  text-transform: capitalize;
  letter-spacing: 4px;
}
#btns{
  display: block;
  margin: auto;
}
.headerBlock{
  display: flex;
  width: 100%;
  background-color: #21232b;
  color: #fff;
  text-align: center;
}
.headerBlock h1{
  letter-spacing: 4px;
  margin: 20px auto;
  text-transform: capitalize;
}
#grid_news{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  padding: 40px 20px;
}
.news_tag{
  position: absolute;
  top: 0;
  left: 0;
  padding: 8px 12px;
  background-color: #00b2a5;
  color: #fff;
}
.Chip_new{
  margin: 15px 0;
}
#Color_picker{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  padding: 10px;
}
.colors_Round{
  height: 35px;
  width: 35px;
  border: 1px solid #f1f1f1f1;
  border-radius: 50%;
  background-color: #ccc;
  margin: 5px 10px;
}
.colors_Round:hover{
  cursor: pointer;
}
#black{
  background-color: #000
}
#white{
  background-color: #fff
}
#blue{
  background-color: blue
}
#red{
  background-color: #f00
}
#green{
  background-color: green;
}
#gray{
  background-color: #ccc
}
#yellow{
  background-color: #ff0
}
#pink{
  background-color: pink
}
#Cyan{
  background-color: cyan
}
#mauve{
  background-color: mediumslateblue
}
#filfter_collection{
  display: flex;
  flex-direction: row;
  position: relative;
}
#filter_grid{
  display: flex;
  padding: 20px;
  width: 100%;
  height: 100%;
  overflow: hidden;
  flex-wrap: wrap;
  justify-content: flex-start;
}
.custom_card{
  display: flex;
  overflow: hidden;
  background-color: #fff;
  height: auto;
  margin: 20px 0;
  min-width: 250px;
  border: 1px solid #f1f1f1;
  width: 25%;
}
.card_text{
  display: block;
  width: 100%;
  text-align: left;
}
.clothes_title{
  margin: 20px 0 0 5px;
  font-size: 16px
}
.clothes_price{
  font-weight: bold;
  margin: 10px 0 10px 10px;
  font-size: 16px;
  color: #3f51b5;
}
#filter_nav{
z-index: 5;
}
.filter_menu{
  display: none !important;
  margin: 10px  !important;
  transition: 1s  !important;
}
.filter_menu:hover{
  cursor: pointer;
  background-color: #505050
}
#close_btn{
  display: none;
  position: absolute;
  top: 0;
  z-index: 2;
  right: 0;
  margin: 10px 20px;
  cursor: pointer;
}

.openFilterMenu{
  transform: translateX(0) !important
}

.closeFilterMenu{
  transform: translateX(-100%) !important
}
.pagination{
  position: relative;
  bottom: 0;
  display: flex;
  width: 100%;
  margin: 40px 0;
}
.Pag{
  display: flex;
  margin: 0 auto;
}
#footer{
  display: flex;
  position: relative;
  margin-top: 300px;
  overflow: hidden;
}
#footer_bg{
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: #21232b;
  z-index: -1;
}
#footer_content{
  display: flex;
  width: 100%;
  flex-direction: row;
  padding: 20px;
  justify-content: flex-start;
  flex-wrap: wrap;
}
.fo_info{
  color: #f1f1f1;
  margin: 10px;
}
.FooterItem{
  display: flex;
  flex-direction: column;
  color: #fff;
  margin: 20px;
}
.FooterItem a{
  text-transform: capitalize;
  color: #f1f1f1f1;
  text-decoration: none; 
}
.FooterItem div a img, .FooterItem div p img{
  margin-right: 10px
}
.FooterItem h3{
  text-transform: uppercase;
  margin-bottom: 30px;
}
.material_f_icons{
  margin-right: 10px;
}
@media(max-width: 1400px){
  .custom_card{
    width: 50%;
  }
}
@media(max-width: 925px){
  .filter_menu{
    display: inline-flex !important;
  }
  #side_nav{
    transform: translateX(-100%);
    position: absolute;
    transition: 1s;
    z-index: 1;
    height: 100%;
  }
  #close_btn{
    display: block;
  }
}
@media(max-width: 560px){
  .custom_card{
    width: 80%;
    margin: 20px auto
  }
  #footer_content{
    flex-direction: column;
  }
}